export default {
	data() {
		return {
			id:"",
			loadStatus:"more",
			pageNum:1,
			pageSize:10,
			total:"",
			list:[],

		}
	},
	onLoad(options) {
		this.id = options.id;
		this.clearList();
		this.getList(1);
	},
	
	methods: {
		reload(){
			this.clearList();
			this.getList(1);
		},
		getList(startPage){
			const that = this;
			let param = {
				startPage: startPage,
				pageSize: that.pageSize,
				visitId: that.id
			}
			that.loadStatus="loading";
			that.$api.housevisitDailyList(param).then(res => {
				if (res.success) {
					let result = res.datas.rows;
					that.total= res.datas.total;
					for(var i in result){
						result[i].headImgUrl = result[i].headImgUrl?result[i].headImgUrl:'../../../../static/images/defaultUserPic.png';
						that.list.push(result[i]);
					}
					let total = res.datas.total;
					let totalPageNum =Math.ceil(total/that.pageSize);
					if(parseInt(totalPageNum)>parseInt(that.pageNum)){
						that.pageNum++;
						that.loadStatus="more";
					}
					else{
						that.loadStatus="noMore";
					}
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
		clearList(){
			const that = this;
			that.pageNum=1;
			that.total="";
			that.list=[];
		},
		
		gotoDetail(id){
			const that = this;
			that.$Router.push({
				path:"/pages/my/manageNavs/journal/detail",
				query:{
					id:id
				}
			})
		},
	},
	onReachBottom() {
		const that = this;
		if(that.loadStatus=="noMore"){
			return;
		}
		else if(that.pageNum!=1){
			that.getList(that.pageNum);
		}
	},
}
